html{
  background-color: white;
  font-family: Merriweather;
 
}
*{
  font-family: Roboto;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a{
  color: black;
  text-decoration: none;
}
.lock{
  border: 10px solid;
  border-color: #FF6D00;
  height: 100%;
}
.homeText{
  font-size: 1.5rem;
  margin-left: 10vw;
  margin-right: 10vw;
  border-bottom: 5px solid #FF6D00;
  margin-top: -10vh;
}

.flexBoxContainer{
  display:flex;
  flex-wrap: wrap;
}
.sign-in {
  color: white;
  background: #FF6D00;
  height: 5vh;
  font-size: 1.5rem;
  float: center;
  margin-top: 2vh;
  width: 50%;
  border: 2px solid white;
  border-radius: 7%;
}
.column{
  justify-content:center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 125px;
}
.columnz{
  justify-content:center;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.row{
  justify-content:center;
  flex-direction: row;
  align-items: flex-end;
  text-align: center;
}

.logo{
  height: 500px;
  width: 500px;
}

h1 {
  font-size: 3rem
}
h2{
  font-size: 2.5rem;
  text-decoration: underline;
}
section {
  background: #f2f4f3;
  height: calc(100vh - 64px);
  display: flex;
  align-items: center;
  justify-content: center;
}

#main-nav a {
  color: black;
  background: #e3dac9;
  height: 7vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  transition: background 0.4s;
  position:sticky;
}
#main-nav a:hover {
  background: grey;
}

#main-nav ul {
  list-style: none;
  display: flex;
}

#main-nav li {
  width: 100%;
  text-align: center;
  position: relative;
}

#main-nav li:hover .submenu > li {
  display: block;
  flex-direction: column;
  top: 0px;
}

.submenu li {
  display: none;
  position: absolute;
  top: 0px;
}

.submenu {
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 100%;
}

.submenu li {
  position: relative;
}

.submenu li:hover .submenu-2 li {
  display: block;
}

.submenu-2 li {
  display: none;
}
.submenu-2 {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0px;
  left: 100%;
  width: 120px;
}

.arrow {
  margin-left: 8px;
  color: black;
  font-size: 12px;
}
.buttonz{
  border-radius: 4px;
  background-color: white;
  border: 5px solid #FF6D00;
  color: black;
  text-align: center;
  font-size: 1.75vw;
  padding: 1vh;
  transition: all 0.5s;
  cursor: pointer;
}
.buttonz span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.buttonz span:after {
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.buttonz:hover span {
  padding: 15px;
}

.buttonz :hover span:after {
  opacity: 1;
  right: 0;
}
.space{
  align-items: center;
  justify-content: center;
}
.instruc{
  font-size: '1vw';
  width: '50vw';
}
h3{
  font-weight: normal;
  padding: 2vw;
}
.fav{
  border-radius: 4px;
  background-color: white;
  border: 5px solid #FF6D00;
  color: red;
  text-align: center;
  font-size: 1.25vw;
  padding: 1vh;
  transition: all 0.5s;
  cursor: pointer;

}

.fav span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.fav span:after {
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.fav:hover span {
  padding: 5px;
}

.fav :hover span:after {
  opacity: 1;
  right: 0;
}
.mealPhoto{
  width: 40vw;
  height: 45vh;
  border:  5px solid #FF6D00;
}

.meal li{
  padding: 0.5vw;
  list-style-type: none;

}
.meal ul{
  padding-left: 5vw;
  padding-right: 5vw;

}
.formz{
  width: 32vw;
  height: 5vh;
  margin-top: 1.5vh;
}
.form{
  margin-top: 5vh
}
.recipeItem{
  height: 20.75vw;
  width: 20.75vw;
  margin-left: 3.35vw;
  margin-bottom: 3.35vh;
}
